/* src/components/Skills.css */
.skills-section {
  padding: 80px 20px;
  background: white;
}

.skills-container {
  max-width: 1200px;
  margin: 0 auto;
}

.section-header {
  text-align: center;
  margin-bottom: 60px;
}

.section-title {
  font-size: 2.5rem;
  color: #1a2980;
  margin-bottom: 10px;
  font-weight: 700;
}

.section-subtitle {
  font-size: 1.2rem;
  color: #666;
  font-weight: 400;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
  margin-bottom: 60px;
}

.skill-category {
  background: #f8f9fa;
  border-radius: 15px;
  padding: 25px;
  border: 1px solid #e9ecef;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.skill-category:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 30px rgba(0,0,0,0.1);
}

.category-header {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 25px;
  padding-bottom: 15px;
  border-bottom: 2px solid #e9ecef;
}

.category-icon {
  font-size: 2rem;
}

.category-title {
  font-size: 1.5rem;
  color: #1a2980;
  margin: 0;
}

.skills-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.skill-item {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.skill-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.skill-name {
  font-weight: 600;
  color: #333;
  font-size: 1.1rem;
}

.skill-level {
  font-weight: 600;
  color: #1a2980;
  font-size: 0.9rem;
}

.skill-bar {
  height: 10px;
  background: #e9ecef;
  border-radius: 5px;
  overflow: hidden;
}

.skill-progress {
  height: 100%;
  border-radius: 5px;
  transition: width 1s ease-in-out;
}

.additional-skills {
  margin-top: 60px;
  padding-top: 40px;
  border-top: 2px solid #f0f0f0;
}

.additional-skills h3 {
  color: #1a2980;
  margin-bottom: 25px;
  font-size: 1.8rem;
  text-align: center;
}

.tools-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  justify-content: center;
}

.tool-tag {
  background: #e9ecef;
  color: #495057;
  padding: 8px 16px;
  border-radius: 20px;
  font-size: 0.9rem;
  font-weight: 500;
  transition: all 0.3s ease;
  border: 1px solid transparent;
}

.tool-tag:hover {
  background: #1a2980;
  color: white;
  transform: translateY(-2px);
  border-color: #1a2980;
  box-shadow: 0 5px 15px rgba(26, 41, 128, 0.2);
}

@media (max-width: 992px) {
  .skills-grid {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 576px) {
  .section-title {
    font-size: 2rem;
  }
  
  .skill-category {
    padding: 20px;
  }
}