/* src/components/Experience.css */
.experience-section {
  padding: 80px 20px;
  background: #f8f9fa;
}

.experience-container {
  max-width: 1200px;
  margin: 0 auto;
}

.experience-timeline {
  position: relative;
  max-width: 800px;
  margin: 0 auto 60px;
}

.experience-item {
  display: flex;
  margin-bottom: 40px;
  position: relative;
}

.timeline-marker {
  position: relative;
  width: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.marker-circle {
  width: 20px;
  height: 20px;
  background: #1a2980;
  border: 4px solid white;
  border-radius: 50%;
  z-index: 2;
  box-shadow: 0 0 0 3px #1a2980;
}

.timeline-line {
  position: absolute;
  top: 20px;
  width: 2px;
  height: calc(100% + 40px);
  background: #1a2980;
  opacity: 0.3;
}

.experience-content {
  flex: 1;
  margin-left: 30px;
  background: white;
  padding: 30px;
  border-radius: 15px;
  box-shadow: 0 5px 20px rgba(0,0,0,0.08);
  transition: transform 0.3s ease;
}

.experience-content:hover {
  transform: translateX(10px);
}

.experience-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 20px;
  flex-wrap: wrap;
  gap: 15px;
}

.company-info h3 {
  color: #1a2980;
  font-size: 1.8rem;
  margin-bottom: 5px;
}

.company-info h4 {
  color: #4CAF50;
  font-size: 1.2rem;
  font-weight: 500;
}

.experience-meta {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 5px;
}

.period {
  background: #1a2980;
  color: white;
  padding: 5px 15px;
  border-radius: 20px;
  font-size: 0.9rem;
  font-weight: 600;
}

.location {
  color: #666;
  font-size: 0.9rem;
}

.experience-description {
  color: #555;
  line-height: 1.7;
  margin-bottom: 25px;
  font-size: 1.1rem;
}

.achievements h5,
.technologies h5 {
  color: #1a2980;
  font-size: 1.2rem;
  margin-bottom: 15px;
  font-weight: 600;
}

.achievements-list {
  list-style: none;
  padding: 0;
  margin-bottom: 25px;
}

.achievement-item {
  display: flex;
  align-items: flex-start;
  gap: 12px;
  margin-bottom: 12px;
  color: #555;
  line-height: 1.6;
}

.achievement-icon {
  color: #4CAF50;
  font-size: 1rem;
  margin-top: 3px;
}

.tech-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.tech-tag {
  background: #e9ecef;
  color: #495057;
  padding: 6px 12px;
  border-radius: 15px;
  font-size: 0.85rem;
  font-weight: 500;
  transition: all 0.3s ease;
}

.tech-tag:hover {
  background: #1a2980;
  color: white;
  transform: translateY(-2px);
}

.experience-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 25px;
  margin-top: 60px;
}

.stat-card {
  background: white;
  padding: 25px;
  border-radius: 15px;
  display: flex;
  align-items: center;
  gap: 20px;
  box-shadow: 0 5px 20px rgba(0,0,0,0.08);
  transition: transform 0.3s ease;
}

.stat-card:hover {
  transform: translateY(-5px);
}

.stat-icon {
  font-size: 2.5rem;
  width: 70px;
  height: 70px;
  background: linear-gradient(135deg, #1a2980, #26d0ce);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

.stat-content {
  flex: 1;
}

.stat-number {
  font-size: 2rem;
  font-weight: bold;
  color: #1a2980;
  margin-bottom: 5px;
}

.stat-label {
  color: #666;
  font-size: 0.95rem;
}

@media (max-width: 768px) {
  .experience-header {
    flex-direction: column;
  }
  
  .experience-meta {
    align-items: flex-start;
  }
  
  .experience-stats {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 576px) {
  .experience-stats {
    grid-template-columns: 1fr;
  }
  
  .stat-card {
    flex-direction: column;
    text-align: center;
    gap: 15px;
  }
}