/* src/components/Profile.css */

/* Profile Section Container */
.profile-section {
  padding: 80px 20px;
  background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
  min-height: 100vh;
}

.profile-container {
  max-width: 1200px;
  margin: 0 auto;
}

/* Profile Header */
.profile-header {
  text-align: center;
  margin-bottom: 60px;
}

.profile-title {
  font-size: 3rem;
  color: #1a2980;
  margin-bottom: 10px;
  font-weight: 700;
}

.profile-subtitle {
  font-size: 1.2rem;
  color: #666;
  font-weight: 400;
}

/* Profile Content Layout */
.profile-content {
  display: grid;
  grid-template-columns: 300px 1fr;
  gap: 50px;
  background: white;
  border-radius: 20px;
  padding: 40px;
  box-shadow: 0 10px 40px rgba(0,0,0,0.1);
}

/* Left Column */
.profile-left {
  border-right: 2px solid #f0f0f0;
  padding-right: 40px;
}

/* Profile Image Section */
.profile-image-container {
  position: relative;
  margin-bottom: 40px;
  text-align: center;
}

.profile-photo {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  object-fit: cover;
  border: 5px solid white;
  box-shadow: 0 10px 30px rgba(0,0,0,0.2);
  transition: all 0.3s ease;
}

.profile-photo:hover {
  transform: scale(1.05);
  box-shadow: 0 15px 40px rgba(0,0,0,0.3);
}

.online-status {
  position: absolute;
  bottom: 20px;
  right: 70px;
  width: 20px;
  height: 20px;
  background: #4CAF50;
  border: 3px solid white;
  border-radius: 50%;
  z-index: 2;
}

.profile-image-upload {
  margin-top: 15px;
}

.upload-btn {
  background: rgba(26, 41, 128, 0.1);
  color: #1a2980;
  border: 2px dashed #1a2980;
  padding: 8px 16px;
  border-radius: 20px;
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.3s ease;
  display: inline-flex;
  align-items: center;
  gap: 8px;
}

.upload-btn:hover {
  background: #1a2980;
  color: white;
  border-color: #1a2980;
}

.upload-icon {
  font-size: 1.2rem;
}

/* CONTACT INFORMATION FIXED SECTION */
.contact-info {
  margin-top: 30px;
}

.contact-info h3 {
  color: #1a2980;
  margin-bottom: 20px;
  font-size: 1.5rem;
  padding-bottom: 10px;
  border-bottom: 2px solid #f0f0f0;
}

/* FIX: Contact item text colors */
.contact-item {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 15px;
  padding: 12px 15px;
  background: #f8f9fa;  /* Light gray background */
  border-radius: 10px;
  border: 1px solid #e9ecef;
  transition: all 0.3s ease;
}

.contact-item:hover {
  background: #e9ecef;  /* Slightly darker on hover */
  transform: translateX(5px);
}

.contact-icon {
  font-size: 1.2rem;
  width: 30px;
  text-align: center;
  color: #1a2980;  /* Blue icon color */
}

/* FIX: Text color for normal text */
.contact-item span {
  color: #333 !important;  /* Dark gray text */
  font-weight: 500;
}

/* FIX: Text color for links */
.contact-item a {
  color: #1a2980 !important;  /* Blue link color */
  text-decoration: none;
  font-weight: 500;
  transition: color 0.3s ease;
}

.contact-item a:hover {
  color: #26d0ce !important;  /* Teal on hover */
  text-decoration: underline;
}

/* Right Column */
.profile-right {
  padding-left: 20px;
}

.profile-name {
  font-size: 2.5rem;
  color: #1a2980;
  margin-bottom: 5px;
}

.profile-role {
  font-size: 1.5rem;
  color: #4CAF50;
  margin-bottom: 30px;
  font-weight: 500;
}

.profile-summary {
  margin-bottom: 40px;
}

.profile-summary h4 {
  color: #1a2980;
  margin-bottom: 15px;
  font-size: 1.3rem;
}

.profile-summary p {
  line-height: 1.8;
  color: #555;
  font-size: 1.1rem;
}

.specialties h4 {
  color: #1a2980;
  margin-bottom: 20px;
  font-size: 1.3rem;
}

.specialties-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 15px;
  margin-bottom: 40px;
}

.specialty-item {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 12px 15px;
  background: #f8f9fa;
  border-radius: 8px;
  border-left: 4px solid #4CAF50;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.specialty-item:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0,0,0,0.1);
}

.specialty-icon {
  color: #4CAF50;
  font-size: 1.2rem;
}

.profile-stats {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  margin-top: 40px;
  padding-top: 30px;
  border-top: 2px solid #f0f0f0;
}

.stat-item {
  text-align: center;
  padding: 15px;
  background: #f8f9fa;
  border-radius: 10px;
  transition: all 0.3s ease;
}

.stat-item:hover {
  background: #e9ecef;
}

.stat-number {
  font-size: 2.5rem;
  font-weight: bold;
  color: #1a2980;
  margin-bottom: 5px;
}

.stat-label {
  color: #666;
  font-size: 0.9rem;
}

/* Responsive Design */
@media (max-width: 992px) {
  .profile-content {
    grid-template-columns: 1fr;
    gap: 40px;
  }
  
  .profile-left {
    border-right: none;
    border-bottom: 2px solid #f0f0f0;
    padding-right: 0;
    padding-bottom: 40px;
  }
  
  .specialties-grid {
    grid-template-columns: 1fr;
  }
  
  .profile-stats {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 576px) {
  .profile-title {
    font-size: 2.2rem;
  }
  
  .profile-stats {
    grid-template-columns: 1fr;
  }
  
  .contact-item {
    flex-direction: column;
    text-align: center;
    gap: 10px;
  }
  
  .contact-item span,
  .contact-item a {
    text-align: center;
  }
}