/* src/components/Projects.css */
.projects-section {
  padding: 80px 20px;
  background: white;
}

.projects-container {
  max-width: 1200px;
  margin: 0 auto;
}

.project-filters {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: center;
  margin-bottom: 40px;
}

.filter-btn {
  padding: 10px 20px;
  background: #f8f9fa;
  border: 2px solid #e9ecef;
  border-radius: 25px;
  color: #495057;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.filter-btn:hover {
  background: #1a2980;
  color: white;
  border-color: #1a2980;
  transform: translateY(-2px);
}

.filter-btn.active {
  background: #1a2980;
  color: white;
  border-color: #1a2980;
  box-shadow: 0 5px 15px rgba(26, 41, 128, 0.2);
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 30px;
  margin-bottom: 60px;
}

.project-card {
  background: #f8f9fa;
  border-radius: 15px;
  padding: 25px;
  border: 1px solid #e9ecef;
  transition: all 0.3s ease;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.project-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 20px 40px rgba(0,0,0,0.1);
  border-color: #1a2980;
}

.project-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 20px;
  flex-wrap: wrap;
  gap: 10px;
}

.project-title {
  color: #1a2980;
  font-size: 1.5rem;
  margin: 0;
  flex: 1;
}

.project-status {
  padding: 4px 12px;
  border-radius: 15px;
  font-size: 0.8rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.project-status.completed {
  background: #d4edda;
  color: #155724;
}

.project-status.in-production {
  background: #d1ecf1;
  color: #0c5460;
}

.project-status.in-progress {
  background: #fff3cd;
  color: #856404;
}

.project-description {
  color: #555;
  line-height: 1.6;
  margin-bottom: 20px;
  flex: 1;
}

.project-technologies {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-bottom: 20px;
}

.tech-badge {
  background: #e9ecef;
  color: #495057;
  padding: 4px 10px;
  border-radius: 12px;
  font-size: 0.8rem;
  font-weight: 500;
}

.project-impact {
  display: flex;
  align-items: center;
  gap: 12px;
  background: white;
  padding: 15px;
  border-radius: 10px;
  margin-bottom: 20px;
  border-left: 4px solid #4CAF50;
}

.impact-icon {
  font-size: 1.5rem;
}

.impact-text {
  color: #333;
  font-size: 0.95rem;
}

.project-actions {
  display: flex;
  gap: 10px;
  margin-top: auto;
}

.btn-details,
.btn-code {
  flex: 1;
  padding: 10px 20px;
  border: none;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.btn-details {
  background: #1a2980;
  color: white;
}

.btn-details:hover {
  background: #0c1a5e;
  transform: translateY(-2px);
}

.btn-code {
  background: white;
  color: #1a2980;
  border: 2px solid #1a2980;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.btn-code:hover {
  background: #1a2980;
  color: white;
  transform: translateY(-2px);
}

.code-icon {
  font-weight: bold;
}

.project-stats {
  display: flex;
  justify-content: center;
  gap: 40px;
  padding-top: 40px;
  border-top: 2px solid #f0f0f0;
}

.project-stats .stat-item {
  text-align: center;
}

.project-stats .stat-value {
  font-size: 2.5rem;
  font-weight: bold;
  color: #1a2980;
  margin-bottom: 5px;
}

.project-stats .stat-label {
  color: #666;
  font-size: 1rem;
}

@media (max-width: 768px) {
  .projects-grid {
    grid-template-columns: 1fr;
  }
  
  .project-stats {
    flex-direction: column;
    gap: 30px;
  }
}