/* src/components/Certifications.css */

/* Certifications Section */
.certifications-section {
  padding: 80px 20px;
  background: linear-gradient(135deg, #ffffff 0%, #f8f9fa 100%);
  position: relative;
  overflow: hidden;
}

.certifications-container {
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
  z-index: 1;
}

/* Section Header */
.section-header {
  text-align: center;
  margin-bottom: 60px;
}

.section-title {
  font-size: 2.8rem;
  color: #1a2980;
  margin-bottom: 15px;
  font-weight: 800;
  position: relative;
  display: inline-block;
}

.section-title::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 4px;
  background: linear-gradient(90deg, #1a2980, #26d0ce);
  border-radius: 2px;
}

.section-subtitle {
  font-size: 1.2rem;
  color: #666;
  font-weight: 400;
  margin-top: 20px;
}

/* Certifications Stats */
.certifications-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 25px;
  margin-bottom: 60px;
}

.stat-card {
  background: white;
  padding: 25px;
  border-radius: 15px;
  display: flex;
  align-items: center;
  gap: 20px;
  box-shadow: 0 5px 20px rgba(0,0,0,0.08);
  transition: all 0.3s ease;
  border: 2px solid transparent;
}

.stat-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 30px rgba(0,0,0,0.12);
  border-color: #1a2980;
}

.stat-icon {
  font-size: 2.5rem;
  width: 70px;
  height: 70px;
  background: linear-gradient(135deg, #1a2980, #26d0ce);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

.stat-content {
  flex: 1;
}

.stat-number {
  font-size: 2.2rem;
  font-weight: bold;
  color: #1a2980;
  margin-bottom: 5px;
  line-height: 1;
}

.stat-label {
  color: #666;
  font-size: 0.95rem;
  font-weight: 500;
}

/* Certifications Grid */
.certifications-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 30px;
  margin-bottom: 60px;
}

.certification-card {
  background: white;
  border-radius: 20px;
  padding: 30px;
  box-shadow: 0 10px 30px rgba(0,0,0,0.1);
  transition: all 0.3s ease;
  border: 1px solid #e9ecef;
  position: relative;
  overflow: hidden;
}

.certification-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 20px 40px rgba(0,0,0,0.15);
}

.certification-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 5px;
  background: linear-gradient(90deg, #1a2980, #26d0ce);
}

.certification-header {
  display: flex;
  align-items: flex-start;
  gap: 20px;
  margin-bottom: 25px;
}

.certification-logo {
  width: 60px;
  height: 60px;
  flex-shrink: 0;
  position: relative;
}

.logo-image {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.logo-placeholder {
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #1a2980, #26d0ce);
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 1.5rem;
  font-weight: bold;
  display: none;
}

.certification-title-container {
  flex: 1;
}

.certification-title {
  color: #1a2980;
  font-size: 1.4rem;
  font-weight: 700;
  margin-bottom: 10px;
  line-height: 1.4;
}

.certification-level {
  padding: 4px 12px;
  border-radius: 15px;
  font-size: 0.8rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.certification-level.expert {
  background: #ffd700;
  color: #856404;
}

.certification-level.advanced {
  background: #4CAF50;
  color: white;
}

.certification-level.intermediate {
  background: #2196F3;
  color: white;
}

/* Certification Details */
.certification-details {
  margin-bottom: 25px;
}

.issuer-info,
.validity-info,
.credential-info {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 15px;
  flex-wrap: wrap;
}

.issuer-label,
.date-label,
.credential-label {
  color: #666;
  font-weight: 600;
  font-size: 0.95rem;
  min-width: 70px;
}

.issuer-name {
  color: #333;
  font-weight: 500;
}

.validity-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.date-info {
  flex: 1;
}

.date-value {
  color: #333;
  font-weight: 500;
  position: relative;
}

.date-value.expired {
  color: #f44336;
}

.expired-badge {
  background: #ffebee;
  color: #c62828;
  padding: 2px 8px;
  border-radius: 10px;
  font-size: 0.75rem;
  font-weight: 600;
  margin-left: 8px;
}

.credential-id {
  background: #f8f9fa;
  padding: 5px 10px;
  border-radius: 6px;
  font-family: 'Courier New', monospace;
  font-size: 0.9rem;
  color: #1a2980;
}

.verified-badge {
  background: #e8f5e9;
  color: #2e7d32;
  padding: 5px 12px;
  border-radius: 15px;
  font-size: 0.85rem;
  font-weight: 600;
  display: inline-flex;
  align-items: center;
  gap: 6px;
  margin-left: auto;
}

.verified-icon {
  font-weight: bold;
  font-size: 1rem;
}

/* Certification Skills */
.certification-skills {
  margin-bottom: 25px;
  padding-top: 20px;
  border-top: 1px solid #e9ecef;
}

.skills-label {
  display: block;
  color: #666;
  font-weight: 600;
  margin-bottom: 12px;
  font-size: 0.95rem;
}

.skills-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.skill-tag {
  background: #e9ecef;
  color: #495057;
  padding: 6px 12px;
  border-radius: 15px;
  font-size: 0.85rem;
  font-weight: 500;
  transition: all 0.3s ease;
}

.skill-tag:hover {
  background: #1a2980;
  color: white;
  transform: translateY(-2px);
}

/* Certification Actions */
.certification-actions {
  display: flex;
  gap: 10px;
}

.btn-view,
.btn-share {
  flex: 1;
  padding: 12px 20px;
  border: none;
  border-radius: 10px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  font-size: 0.95rem;
}

.btn-view {
  background: #1a2980;
  color: white;
}

.btn-view:hover {
  background: #0c1a5e;
  transform: translateY(-2px);
}

.btn-share {
  background: white;
  color: #1a2980;
  border: 2px solid #1a2980;
}

.btn-share:hover {
  background: #1a2980;
  color: white;
  transform: translateY(-2px);
}

.btn-icon {
  font-size: 1.1rem;
}

/* Certification Progress */
.certification-progress {
  background: white;
  border-radius: 20px;
  padding: 40px;
  margin-bottom: 40px;
  box-shadow: 0 10px 30px rgba(0,0,0,0.1);
}

.progress-title {
  color: #1a2980;
  font-size: 1.8rem;
  margin-bottom: 30px;
  font-weight: 700;
  display: flex;
  align-items: center;
  gap: 10px;
}

.progress-title::before {
  content: 'ðŸŽ¯';
  font-size: 1.5rem;
}

.progress-track {
  display: flex;
  flex-direction: column;
  gap: 25px;
}

.progress-item {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.progress-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.progress-label {
  color: #333;
  font-weight: 600;
  font-size: 1.1rem;
}

.progress-percentage {
  color: #1a2980;
  font-weight: 700;
  font-size: 1.2rem;
}

.progress-bar {
  height: 12px;
  background: #e9ecef;
  border-radius: 6px;
  overflow: hidden;
  position: relative;
}

.progress-fill {
  height: 100%;
  background: linear-gradient(90deg, #1a2980, #26d0ce);
  border-radius: 6px;
  transition: width 1s ease-in-out;
  position: relative;
}

.progress-fill::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(90deg, 
    rgba(255,255,255,0.4) 0%, 
    rgba(255,255,255,0.1) 50%, 
    rgba(255,255,255,0.4) 100%);
  animation: shimmer 2s infinite;
}

@keyframes shimmer {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

/* Learning in Progress */
.learning-progress {
  background: linear-gradient(135deg, #1a2980, #26d0ce);
  border-radius: 20px;
  padding: 40px;
  color: white;
}

.learning-title {
  font-size: 1.8rem;
  margin-bottom: 30px;
  font-weight: 700;
  display: flex;
  align-items: center;
  gap: 10px;
}

.learning-title::before {
  content: 'ðŸ“š';
  font-size: 1.5rem;
}

.learning-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 25px;
}

.learning-card {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border-radius: 15px;
  padding: 25px;
  display: flex;
  gap: 20px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  transition: all 0.3s ease;
}

.learning-card:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: translateY(-5px);
}

.learning-icon {
  font-size: 2.5rem;
  width: 60px;
  height: 60px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.learning-content {
  flex: 1;
}

.learning-content h4 {
  font-size: 1.3rem;
  margin-bottom: 10px;
  font-weight: 600;
}

.learning-content p {
  color: rgba(255, 255, 255, 0.9);
  margin-bottom: 15px;
  font-size: 0.95rem;
}

.learning-progress-bar {
  height: 8px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 4px;
  overflow: hidden;
}

.learning-fill {
  height: 100%;
  background: white;
  border-radius: 4px;
  transition: width 1s ease-in-out;
}

/* Responsive Design */
@media (max-width: 1200px) {
  .certifications-grid {
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  }
}

@media (max-width: 992px) {
  .section-title {
    font-size: 2.3rem;
  }
  
  .certifications-stats {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .learning-cards {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 768px) {
  .certifications-section {
    padding: 60px 15px;
  }
  
  .certifications-grid {
    grid-template-columns: 1fr;
    gap: 25px;
  }
  
  .certification-header {
    flex-direction: column;
    text-align: center;
    gap: 15px;
  }
  
  .certification-logo {
    margin: 0 auto;
  }
  
  .certification-stats {
    grid-template-columns: 1fr;
  }
  
  .certification-progress,
  .learning-progress {
    padding: 30px 20px;
  }
  
  .certification-actions {
    flex-direction: column;
  }
  
  .validity-info {
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
  }
}

@media (max-width: 576px) {
  .section-title {
    font-size: 2rem;
  }
  
  .stat-card {
    flex-direction: column;
    text-align: center;
    gap: 15px;
  }
  
  .learning-card {
    flex-direction: column;
    text-align: center;
  }
  
  .learning-icon {
    margin: 0 auto;
  }
}